<template>
     
</template>

<script>
import { Toast, Indicator } from 'mint-ui'
export default {
	data() {
	    return {
	    	
	    }
	},
	created() {
		const self = this
		window.getOpenKey(function(msg){
			Indicator.open()
		    self.$api.wanbaLogin({
	    		platform: window.OPEN_DATA.platform,
	    		openid: msg.data.openid,
	    		openkey: msg.data.openkey
	    	}).then(res => {
	    		Indicator.close()
		        let accessToken = res.data.accessToken
				self.$token.refreshToken(accessToken.accessToken, accessToken.refreshToken, accessToken.expireTime)
				self.$storage.set('hx', {id: res.data.hxId, password: res.data.hxPwd})
				self.$storage.set('user', res.data)
				self.$storage.set('headUrl', res.data.avatar || '../../static/image/vvv.png')
//				self.$storage.set('isNew', res.data.isNew)
				self.$storage.set('isNew', true)
				window.location.hash='/index'
//				self.$mqqOpenUrl(mqq, 'index', 0, 0)
		    }, err => {
		        Indicator.close()
		    })
		});
			
	},
	methods: {
		
	},
	

		
}
</script>

<style lang="scss" scoped>
.app{
	background: #fff !important;
	position: relative;
	height: 100vh;
	.top{
		position: absolute;
		width: 100%;
		height: 65vh;
		left:0;
		top:0;
		
		.bg{
			background: #ffed8d;
			width: 100%;
			height: 50vh;
		}
		img{
			position: absolute;
			width: 100%;
			left:0;
			bottom:0;
			z-index: 3;
		}
	}
	.bottom{
		position: absolute;
		width: 100%;
		height: 35vh;
		left:0;
		bottom:0;
		padding: 1rem 2rem 0;
		.login-tit{
			text-align: center;
			color: #222;
			position: relative;
			&:before{
				content: '';
				position: absolute;
				left: -0.4rem;
				top: 50%;
				transform: translateY(-50%);
				height: 0;
				border-top: 1px solid #ccc;
				width: 1rem;
			}
			&:after{
				content: '';
				position: absolute;
				right: -0.4rem;
				top: 50%;
				transform: translateY(-50%);
				height: 0;
				border-top: 1px solid #ccc;
				width: 1rem;
			}
		}
		.login-way{
			display: flex;
			align-items: center;
			justify-content: space-between;
			padding-top: 0.6rem;
			img{
				width: 1.1rem;
				
			}
		}
		
	}
} 
.miss{
	position: fixed;
	right: 0.1rem;
	top: 0.2rem;
	font-size: 0.3rem;
	color: #704a06;
	z-index: 10;
	padding: 0.2rem;
}
</style>


